.block-info {
  display: flex;
  flex-direction: column;

  @include onTable() {
    flex-direction: row;
    gap: 20px;

    &--reverse {
      flex-direction: row-reverse;
    }
  }

  &__poster {
    margin-block: 30px 20px;

    @include onTable() {
      width: 50%;
      flex: 1;
      margin-block: 0;
    }

    @include onDesktop() {
      height: 388px;
      object-fit: cover;
    }
  }

  &__content {
    flex: 1;
  }

  &__title {
    color: $block-info-title-color;

    @include titleSection();

    &--onMobile {
      @include onTable() {
        display: none;
      }
    }

    &--onTable {
      display: none;

      @include onTable() {
        display: block;
      }
    }
  }

  &__text {
    color: $block-info-text-color;

    @include textMain();

    @include onTable() {
      margin-block: 30px 60px;
    }
  }

  &__button {
    position: relative;
    color: $button-about-us-text-color;
    width: max-content;

    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;

    margin-top: 20px;

    @include hoverUnderline();
  }
}
